.buttonContainer {
  display: flex;
  flex-direction: column;
  margin-top: 100px;
  margin-bottom: 100px;
  
}

.textButton {
  background: none;
  color: black;
  border: none;
  cursor: pointer;
  margin: 8px 0; /* 버튼 사이 간격 */
}

.textButton:hover {
  text-decoration: underline; /* 마우스 호버 시 밑줄 추가 */
}

.filledButton {
  background-color: rgb(52, 170, 13); /* 예시 색상 */
  color: white;
  border: none;
  cursor: pointer;
  margin: 80px 0; /* 버튼 사이 간격 */
  padding: 10px;
}

.filledButton:hover{
  background-color: rgb(43, 145, 9); /* 예시 색상 */
  color: white;
  border: none;
  cursor: pointer;
  margin: 80px 0; /* 버튼 사이 간격 */
  padding: 10px;

}