.container {
  background-color: #E3ECF1;
}
.sidebar {
  background-color: #E3ECF1;
  border-left: 4px solid #202020;
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  transition: 0.4s ease;
  color: #202020;
  height: 100%;
  z-index: 99;
}
.button {
  position: relative;
  left: -50px;
  top: 10px;
  width: 40px;
  height: 40px;
  z-index: 99;
  transition: 0.8s ease;
  border: 2px solid #202020;
  border-radius: 40px;
  overflow: hidden;

}
.openBtn {
  width: 100%;
  height: 100%;
  pointer-events: none;
}
.content {
  padding: 40px 40px 0 20px;
  position: relative;
  width: 100%;
}
.icon {
  margin: 0;
  color: #202020;
}

.inBoxButton button {
  margin-left: 20px;
  border-radius: 50%;
  background-color: yellow; /* 버튼 배경색 설정 */
  color: black; /* 글씨 색 설정 */
  font-weight: bold; /* 글씨 두껍게 */
  margin-right: 40px; /* 오른쪽 버튼과의 간격 설정 */
  border: none; /* 테두리 제거 */
  padding: 10px 20px; /* 내부 여백 설정 */
  cursor: pointer; /* 마우스 오버 시 커서 변경 */
  transition: background-color 0.3s ease; /* 배경색 변경 애니메이션 */
}

.inBoxButton button:last-child {
  margin-right: 0; /* 마지막 버튼의 오른쪽 마진 제거 */
}

.inBoxButton button:hover {
  background-color: #ffcc00; /* 호버 시 배경색 변경 */
}

